/* eslint-disable @typescript-eslint/no-explicit-any */

/*
Usage:
------------
<subscription-timer>
<template v-slot:default="prop" v-if="digitalData.subscription != null && digitalData.subscription.termtype != null && digitalData.subscription.termtype == '1'">
Your subscription will get expire in {{prop.days}} DAYS {{prop.hours}} HOURS {{prop.minutes}} MINUTES {{prop.seconds}} SECONDS
</template>
<template v-slot:default="prop" v-if="digitalData.subscription != null && digitalData.subscription.termtype != null && digitalData.subscription.termtype == '2'">
Your subscription expired {{prop.days}} DAYS {{prop.hours}} HOURS {{prop.minutes}} MINUTES {{prop.seconds}} SECONDS ago
</template>
</subscription-timer>
 */
import { defineComponent, ref, computed, onBeforeUnmount } from "vue";
export const subscriptionTimer = defineComponent({
    template: `<span><slot :days="days" :hours="hours" :minutes="minutes" :seconds="seconds"></slot></span>`,
    setup() {
        const tempInterval = setInterval(() => startSubscriptionTimer(), 100);
        const timeDiff = ref(0);
        let timerInterval: any = null;
        // Methods
        const startSubscriptionTimer = () => {
            const start = window.digitalData?.subscription?.startDateUTC || undefined,
                end = window.digitalData?.subscription?.expiryDateUTC || undefined;
            if (start && end && start.trim() !== "" && end.trim() !== "") {
                clearInterval(tempInterval);
                const startDt = new Date(start),
                    endDt = new Date(end);
                endDt.setSeconds(endDt.getSeconds() - 1);
                timerCount(startDt, endDt);
                // Start timer
                timerInterval = setInterval(() => {
                    endDt.setSeconds(endDt.getSeconds() - 1);
                    timerCount(startDt, endDt);
                }, 1000);
            }
        };

        const timerCount = (start: any, end: any) => {
            const diff = end - start;
            if (diff < 0) {
                timeDiff.value = Math.abs(diff);
            } else {
                timeDiff.value = diff;
            }
        };

        //Computed
        const days = computed(() => Math.floor(timeDiff.value / (1000 * 60 * 60 * 24)));
        const hours = computed(() => Math.floor((timeDiff.value % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)));
        const minutes = computed(() => Math.floor((timeDiff.value % (1000 * 60 * 60)) / (1000 * 60)));
        const seconds = computed(() => Math.floor((timeDiff.value % (1000 * 60)) / 1000));

        onBeforeUnmount(() => {
            clearInterval(timerInterval);
        });

        return { days, hours, minutes, seconds };
    },
});
