/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/naming-convention */
import { logErrorMessage, logToServer } from "../../core/error-handler/globalErrorHandler";
import { ERROR_TYPE, ERROR_MESSAGES, CLIENT_LOG_PROP } from "../../core/constants/commonConstants";

function getHeaders(optionalHeaders: any): Record<any, any> {
    const headers = {
        "content-type": "application/json",
        "X-Application-Type": window.digitalData?.page?.applicationType || "",
        "X-Flow-Type": window.digitalData?.page?.flowType || "",
    };

    return { ...headers, ...optionalHeaders };
}

function customizedFetch(url: string, optionalHeaders: any): Promise<Response> {
    return fetch(url, {
        method: "GET",
        headers: getHeaders(optionalHeaders),
        //There is a need to pass credentials to fetch. This is done to make API calls work in Edge 42.
        //https://github.com/github/fetch/tree/3674c98df696d45573750aa7873814887d25689a#sending-cookies
        credentials: "same-origin",
    });
}

export function asyncGET(url: string): Promise<Response> {
    return fetch(url).then((response) => {
        if (response && response.status === 200) {
            return response.json();
        }
        const errorDetail = {
            status: response?.status || "",
            statusText: response?.statusText || "",
            url: response?.url || "",
        };
        return Promise.reject(errorDetail);
    });
}

export function asyncGETAll(urls: Array<string>): Promise<any> {
    return Promise.all(urls.map((url) => customizedFetch(url, {})))
        .then(function (responses) {
            return Promise.all(
                responses.map(function (response: Response) {
                    const contentType = response.headers.get("content-type");
                    if (!contentType || !contentType.includes("application/json")) {
                        logErrorMessage(
                            ERROR_TYPE.dataError,
                            ERROR_MESSAGES.restCallFailed,
                            "",
                            "Rest call failed for URL as the response contentType is " + contentType,
                            "",
                            logToServer(CLIENT_LOG_PROP.dataError),
                        );
                        return Promise.resolve(false);
                    }
                    return response.json();
                }),
            );
        })
        .then(function (data) {
            return data.reduce((r, c) => ({ ...r, ...c }), {});
        })
        .catch(function (error) {
            logErrorMessage(
                ERROR_TYPE.dataError,
                ERROR_MESSAGES.restCallFailed,
                "",
                "Rest call failed for URL " + error + " with status code: " + " with status Text: ",
                "",
                logToServer(CLIENT_LOG_PROP.dataError),
            );
        });
}

export function asyncPOST(url: string, data: {}, optionalHeaders: {}): Promise<Response> {
    return fetch(url, {
        method: "POST",
        headers: getHeaders(optionalHeaders),
        body: JSON.stringify(data),
    }).then((response) => {
        if (response && response.status === 200) {
            return response.json();
        }
        const errorDetail = {
            status: response?.status || "",
            statusText: response?.statusText || "",
            url: response?.url || "",
        };
        return Promise.reject(errorDetail);
    });
}
