import { analyticsHandler } from "./registerEvents";
import { getDigitalData, setDigitalData } from "./utilities/digitalData";
// eslint-disable-next-line @typescript-eslint/no-unused-vars
function fireEvent(linkdata: AnalyticsFireData, initiator: string, args: unknown) {
    if (!linkdata) {
        return;
    }
    const currentLinkData: AnalyticsFireData = linkdata;
    const utagData: AnalyticsConfig = getDigitalData("digitalData.analytics.utagData");
    for (const key in utagData) {
        // Getting fireable tags from config
        if (utagData[key]?.value && utagData[key]?.value !== "" && utagData[key]?.event === "all") {
            currentLinkData[key] = utagData[key].value.toString().toLowerCase();
        } else if (utagData[key]?.value && utagData[key]?.value !== "" && utagData[key]?.event === "specificEvents") {
            if (utagData[key]?.specificEvents?.length > 0 && utagData[key]?.specificEvents?.indexOf(initiator) !== -1) {
                currentLinkData[key] = utagData[key].value.toString().toLowerCase();
            }
        }
    }
    window.utag?.link(currentLinkData);
    try {
        const callback = getDigitalData(`digitalData.analytics.events.${initiator}.callbackFunc`);
        if (callback) {
            // eslint-disable-next-line no-eval
            eval(callback);
        }
    } catch (error) {
        console.log(`There was an error while executing a callback function
                    Error Stack: ${error.message}`);
    }
}
function fireDependantTags(dependantTags: string[], args: unknown) {
    dependantTags.forEach((tag: string) => {
        const dependancyData: AnalyticsFireData = {};
        const currentDependancy = getDigitalData(`digitalData.analytics.events.${tag}`);
        if (currentDependancy?.fired === "false") {
            dependancyData[tag] = currentDependancy.value;
            fireEvent(dependancyData, tag, args);
            setDigitalData({
                key: `digitalData.analytics.events.${tag}.fired`,
                value: "true",
            });
            setDigitalData({
                key: `digitalData.analytics.events.${tag}.timesFired`,
                value: `${parseInt(currentDependancy.timesFired) + 1}`,
            });
        }
    });
}
function prepareLinkData(
    tag: string,
    tagData: AnalyticsTagConfig,
    condition: string,
    el: unknown,
    args: unknown,
): void {
    if (!tag && !tagData) {
        return;
    }
    const fireData: AnalyticsFireData = {};
    const validate: string = condition ? condition : tagData.condition;
    if (validate) {
        try {
            // eslint-disable-next-line no-eval
            if (eval(validate)) {
                if (tagData?.sequenceDependancy?.length > 0) {
                    fireDependantTags(tagData.sequenceDependancy, args);
                }
                fireData[tag] = tagData.value;
                fireEvent(fireData, tag, args);
                setDigitalData({
                    key: `digitalData.analytics.events.${tag}.fired`,
                    value: "true",
                });
                if (tagData.timesFired) {
                    setDigitalData({
                        key: `digitalData.analytics.events.${tag}.timesFired`,
                        value: `${parseInt(tagData.timesFired) + 1}`,
                    });
                }
            }
        } catch (error) {
            console.log(`There was an error while eval the condition.
                    Error Stack: ${error.message}`);
        }
    } else {
        fireData[tag] = tagData.value;
        fireEvent(fireData, tag, args);
        setDigitalData({
            key: `digitalData.analytics.events.${tag}.fired`,
            value: "true",
        });
        if (tagData.timesFired) {
            setDigitalData({
                key: `digitalData.analytics.events.${tag}.timesFired`,
                value: `${parseInt(tagData.timesFired) + 1}`,
            });
        }
    }
}

// eslint-disable-next-line @typescript-eslint/no-explicit-any
window.handleAnalytics = (data: { event: CustomEvent; value: AnalyticsEventInput; element: any }) => {
    const args: unknown = data.event?.detail;
    const eventType = data.event?.type;
    const el = data.element;
    // tealiumData.getViewDataFromConfig(vueIns, args);
    if (data.value[eventType]?.data) {
        for (const key in data.value[eventType].data) {
            if (getDigitalData(`digitalData.analytics.utagData.${key}`)) {
                setDigitalData({
                    key: `digitalData.analytics.utagData.${key}.value`,
                    // eslint-disable-next-line no-eval
                    value: eval(data.value[eventType].data[key]),
                });
            } else if (getDigitalData(`digitalData.analytics.events.${key}`)) {
                setDigitalData({
                    key: `digitalData.analytics.events.${key}.value`,
                    // eslint-disable-next-line no-eval
                    value: eval(data.value[eventType].data[key]),
                });
            }
        }
    }
    if (data.value[eventType].tags) {
        Object.keys(data.value[eventType].tags).forEach((tag) => {
            const tagValues = getDigitalData(`digitalData.analytics.events.${tag}`);
            const condition = data.value[eventType].tags[tag].condition;
            if (tagValues?.fired === "false" || tagValues?.fireCount === "multiple") {
                if (tagValues?.type === "group") {
                    if (tagValues?.fireWhen === "anyone") {
                        if (tagValues?.fireCount !== "once") {
                            prepareLinkData(tag, tagValues, condition, el, args);
                        } else if (tagValues?.fireCount === "once") {
                            if (tagValues?.fired === "false") {
                                prepareLinkData(tag, tagValues, condition, el, args);
                            }
                        }
                    }
                    if (tagValues?.fireWhen === "all") {
                        prepareLinkData(tag, tagValues, condition, el, args);
                    }
                }
                if (tagValues.type && tagValues.type === "individual") {
                    if (tagValues.fireCount && tagValues.fireCount !== "once") {
                        prepareLinkData(tag, tagValues, condition, el, args);
                    } else if (tagValues.fireCount && tagValues.fireCount === "once" && tagValues.fired === "false") {
                        prepareLinkData(tag, tagValues, condition, el, args);
                    }
                }
            } else {
                if (data.event.type === "keypress") {
                    // $(el).off("keypress");
                    el.removeEventListener("keypress", analyticsHandler);
                }
            }
        });
    }
};
const handleAnalytics = window.handleAnalytics;
export { handleAnalytics, prepareLinkData };
