/* eslint-disable @typescript-eslint/no-explicit-any */
import { addToCart } from "./addToCart";
import { offerSegmentRules } from "./offerSegmentRules";

export const methods: Record<any, any> = {
    getPrice(val1: string, val2: string, defaultVal: string): string {
        return window.getPrice(val1, val2, defaultVal);
    },
    getUrl(url: string, enableQS = false) {
        return window.getUrl(url, enableQS);
    },
    ...addToCart,
    ...offerSegmentRules,
};
