function storeDynamicModals(offer: string): void {
    if (offer && offer !== "") {
        if (offer.includes("=")) {
            //User binded modelname=offer
            const obj: Array<string> = offer.split("=");
            if (window.digitalData.offersGroup[obj[0]] === undefined) {
                window.digitalData.offersGroup[obj[0]] = obj[1];
            }
        } else {
            //User binded direct offer
            if (window.digitalData.offersGroup[offer] === undefined) {
                window.digitalData.offersGroup[offer] = offer;
            }
        }
    }
}

function getMoguid(offer: string, buttonSection: string): string {
    const metaOffer: string = window.digitalData?.offers?.[offer]?.metaOfferGuid || "";
    if (metaOffer === "") {
        console.log("Meta offer is empty for button section " + buttonSection);
    }
    return metaOffer;
}

function setAdditionalQS(qp: URLSearchParams, addqs: string): void {
    const splitWord = addqs?.split("&") || "";
    for (let i = 0; i < splitWord.length; i++) {
        const dta = splitWord[i].split("=");
        if (dta[0] && dta[1]) {
            if (dta[0] !== "culture") {
                qp.set(dta[0], dta[1]);
            }
        }
    }
}
function isNullOrEmpty(value: unknown) {
    return !value;
}
function getAbbreviatedValue(value: string): string {
    switch (value.toLowerCase()) {
        case "landingpages":
            return "lp";
        case "retention":
            return "ret";
        case "expiry":
            return "exp";
        case "paidSearch":
            return "ps";
        case "mss+":
            return "mss";
        default:
            return value;
    }
}
function getCampaignPName() {
    const analytics = window.digitalData.analytics;
    if (!analytics) {
        return "";
    }

    const campaignPName = [];
    const culture = window.digitalData.culture?.culture || window.digitalData.qs?.culture;
    campaignPName.push(culture || "en-us");
    if (analytics.siteSection) {
        campaignPName.push(getAbbreviatedValue(analytics.siteSection));
    }
    if (analytics.siteSectionLevel2) {
        campaignPName.push(getAbbreviatedValue(analytics.siteSectionLevel2));
    }
    if (analytics.siteSectionLevel3) {
        campaignPName.push(getAbbreviatedValue(analytics.siteSectionLevel3));
    }
    campaignPName.push(window.digitalData.page?.name || "");
    return campaignPName.join(":").toLowerCase().replaceDash();
}
function getExperienceBySection(section: string): string {
    if (!window.digitalData.experiences?.selectedExperiences) {
        return "";
    }
    const selectedExperience = window.digitalData.experiences?.selectedExperiences?.filter(
        (ex: any) => ex.type === section,
    );
    if (!selectedExperience || !selectedExperience[0]) {
        return "";
    }

    return selectedExperience[0].experienceTitle?.toLowerCase().replaceDash();
}
function getHeroExperience(): string {
    const heroExperience = getExperienceBySection("hero");
    return heroExperience === "" ? "no-hero-exp" : heroExperience;
}
function getOfferSegmentName() {
    if (!window.digitalData.offerSegmentRules) {
        return "";
    }
    const rule = window.digitalData.offerSegmentRules.rules.filter((ex: any) => ex.evalResult === true)[0];

    if (!rule) {
        return "";
    }
    return rule.name?.toLowerCase().replaceDash();
}
function getCampaignPUName(): string {
    return getCampaignPName() + "@" + getHeroExperience() + ":" + getOfferSegmentName();
}
function getCampaignPUNameforButton(section: string): string {
    return getCampaignPName() + "@" + getExperienceBySection(section) + ":" + getOfferSegmentName();
}
function getUOMValue(uom: string) {
    if (!uom) {
        return "y";
    }

    switch (uom) {
        case "1":
            return "d";
        case "2":
            return "m";
        case "7":
            return "u";
        default:
            return "y";
    }
}
function getPlanType(offer: any) {
    if (offer.unitValue === "-1") {
        return offer.unitValue;
    }

    return offer.unitValue + getUOMValue(offer.unitOfMeasurement) + (offer.planType === "1" ? "t" : "p");
}
function getCTAName(selectedOffer: any, section: string) {
    selectedOffer = window.digitalData?.offers?.["o1"] || undefined;
    if (!selectedOffer) {
        return "";
    }
    return [
        selectedOffer.packageCode,
        getPlanType(selectedOffer),
        selectedOffer.uf.finalPrice,
        selectedOffer.currencyCode,
        section,
    ]
        .join(":")
        .toLowerCase();
}
function isNGMRenewal(rnwsrc: any) {
    if (!rnwsrc) {
        return false;
    }
    const renewSource = ["ngm"];
    if (renewSource.includes(rnwsrc.toLowerCase())) {
        return true;
    }
    return false;
}
function isProductRenewal(rnwsrc: any) {
    if (!rnwsrc) {
        return false;
    }
    const renewSource = ["msc", "msf", "mra", "wsc", "mma", "unpsn", "upao"];
    if (renewSource.includes(rnwsrc.toLowerCase())) {
        return true;
    }
    return false;
}
function isEmailRenewal(querystring: any) {
    if (!querystring) {
        return false;
    }

    if (
        !isNullOrEmpty(querystring["_rid"]) ||
        !isNullOrEmpty(querystring["_mid"]) ||
        !isNullOrEmpty(querystring["extra_info"]) ||
        !isNullOrEmpty(querystring["extra_info2"])
    ) {
        return true;
    }
    return false;
}
function isSemRenewal(querystring: any) {
    if (!querystring) {
        return false;
    }

    if (
        !isNullOrEmpty(querystring["utm_source"]) ||
        !isNullOrEmpty(querystring["utm_medium"]) ||
        !isNullOrEmpty(querystring["utm_campaign"]) ||
        !isNullOrEmpty(querystring["utm_content"]) ||
        !isNullOrEmpty(querystring["utm_term"])
    ) {
        return true;
    }
    return false;
}
function isSeoRenewal(querystring: any) {
    if (!querystring) {
        return false;
    }

    if (!isNullOrEmpty(querystring["ss"])) {
        return true;
    }
    return false;
}
function getGlobalSource(): string {
    const queryString = window.digitalData.qs;
    if (!queryString) {
        return "";
    }

    if (isNGMRenewal(queryString.rnwsrc)) {
        return "ngm";
    }

    if (isProductRenewal(queryString.rnwsrc)) {
        return "product";
    }

    if (isEmailRenewal(queryString)) {
        return "email";
    }

    if (isSemRenewal(queryString)) {
        return "sem";
    }

    if (isSeoRenewal(queryString)) {
        return "seo";
    }

    return "other";
}
function getNGMSourceLevel2(clicksrc: string) {
    const clickSourceAlert: any = [];
    const clickSourceCoreUI: any = [];

    if (!clicksrc) {
        return "unspecified";
    }

    if (clickSourceAlert.includes(clicksrc.toLowerCase()) || clicksrc.toLowerCase().includes("alert")) {
        return "alert";
    }
    if (clickSourceCoreUI.includes(clicksrc.toLowerCase()) || clicksrc.toLowerCase().includes("coreui")) {
        return "coreui";
    }

    return "unspecified";
}
function getProductSourceLevel2(clicksrc: string) {
    const clickSourceAlert: any = [];
    const clickSourceCoreUI = ["subscriptionScreen", "renewmscpostexiryinfoviz"];

    if (!clicksrc) {
        return "unspecified";
    }

    if (clickSourceAlert.includes(clicksrc.toLowerCase()) || clicksrc.toLowerCase().includes("alert")) {
        return "alert";
    }

    if (clickSourceCoreUI.includes(clicksrc.toLowerCase()) || clicksrc.toLowerCase().includes("subscription")) {
        return "coreui";
    }

    return "unspecified";
}
function getSeoSourceLevel2(querystring: any) {
    if (!querystring || !isNullOrEmpty(querystring["avm"])) {
        return "other";
    }

    if (querystring["avm"] === 0) {
        return "not-mcafee";
    }

    if (querystring["avm"] === 1) {
        return "mcafee";
    }

    return "other";
}
function getSourceLevel2(source: string): string {
    const clicksrc = window.digitalData.qs.clicksrc;

    if (source === "ngm") {
        return getNGMSourceLevel2(clicksrc);
    }

    if (source === "product") {
        return getProductSourceLevel2(clicksrc);
    }

    if (source === "email") {
        return "";
    }

    if (source === "sem") {
        return "";
    } //brand, unbrand

    if (source === "seo") {
        return getSeoSourceLevel2(window.digitalData.qs);
    }

    return "other";
}
window.analyticsData = {
    getGlobalSource: function (): string {
        return getGlobalSource();
    },
    getSourceLevel2: function (): string {
        return getSourceLevel2(getGlobalSource());
    },
    getCampaignPName: function (): string {
        return getCampaignPName();
    },
    getCampaignPUName: function (): string {
        return getCampaignPUName();
    },
};
function setCDWParamsToButton(queryParams: URLSearchParams, offerBind: string, section: string): void {
    const campaignPUName = getCampaignPUNameforButton(section);
    const ccta = getCTAName(offerBind, section);
    let campaignType = "";
    let campaignSubType = "";
    const culture = queryParams.get("culture");

    campaignType = window.digitalData.analytics?.campaignType?.toLowerCase() || "";
    campaignSubType = window.digitalData.analytics?.campaignSubType?.toLowerCase() || "";

    if (!culture) {
        queryParams.set("culture", window.digitalData.culture?.culture || "");
    }

    const queryString = window.digitalData.qs;
    const globalSource = getGlobalSource();
    queryParams.set("cctype", isNullOrEmpty(queryString["cctype"]) ? campaignType : queryString.cctype);
    queryParams.set("ccstype", isNullOrEmpty(queryString["ccstype"]) ? campaignSubType : queryString.ccstype);
    queryParams.set("cseg", isNullOrEmpty(queryString["cseg"]) ? getOfferSegmentName() : queryString.cseg);
    queryParams.set("cexp", getExperienceBySection(section));
    queryParams.set("ccta", ccta);
    queryParams.set("ccpubn", campaignPUName + "@" + ccta);
    queryParams.set("ccpun", campaignPUName);
    queryParams.set("ccpn", getCampaignPName());
    queryParams.set("csrc", isNullOrEmpty(queryString["csrc"]) ? globalSource : queryString.csrc);
    queryParams.set(
        "csrcl2",
        isNullOrEmpty(queryString["csrcl2"]) ? getSourceLevel2(globalSource) : queryString.csrcl2,
    );
    queryParams.set("cls", isNullOrEmpty(queryString["cls"]) ? "0" : queryString.cls);
    queryParams.set("cupf", isNullOrEmpty(queryString["cupf"]) ? "0" : queryString.cupf);
    queryParams.set("cafcat", isNullOrEmpty(queryString["cafcat"]) ? "" : queryString.cafcat);
    queryParams.set("ccoe", isNullOrEmpty(queryString["ccoe"]) ? "" : queryString.ccoe);
    queryParams.set("ccoel2", isNullOrEmpty(queryString["ccoel2"]) ? "" : queryString.ccoel2);
    queryParams.set("ak_culture", isNullOrEmpty(queryString["ak_culture"]) ? "" : queryString.ak_culture);
    queryParams.set("ipst", isNullOrEmpty(queryString["ipst"]) ? "" : queryString.ipst);
    queryParams.set("ipcon", isNullOrEmpty(queryString["ipcon"]) ? "" : queryString.ipcon);
    queryParams.set(
        "tm_local_lp_ab_test_variant",
        isNullOrEmpty(queryString["tm_local_lp_ab_test_variant"]) ? "" : queryString.tm_local_lp_ab_test_variant,
    );
}

function getUpdatedOffer(offer: string): string {
    if (offer.split("=").length > 1) {
        const key = offer.split("=")[0];
        return window.digitalData?.offersGroup?.[key] || undefined;
    }
    return window.digitalData?.offersGroup?.[offer] || undefined;
}
export function registerGetCartUrl(): void {
    window.getCartURL = function (offer: string, additionalQS: string, buttonSection: string): string {
        /*  if (window.vueInstance) {
            if (window.vueInstance.vueLoaded === false) {
                return "";
            }
        } */
        storeDynamicModals(offer);
        offer = getUpdatedOffer(offer);
        if (window.digitalData.offers && Object.keys(window.digitalData.offers).length === 0) {
            return "";
        }
        const urlParams = new URLSearchParams(location.search);
        const cartUrl = window.digitalData?.cart?.cartUrl || "";
        const metaOffer = getMoguid(offer, buttonSection);
        if (metaOffer !== "") {
            urlParams.set("moguid", metaOffer);
        }

        urlParams.set("culture", window.digitalData?.culture?.culture || "en-us");

        if (window.digitalData?.affiliate?.affid !== "") {
            urlParams.set("affid", window.digitalData.affiliate.affid);
        }
        const osnPath: string =
            window.digitalData?.offerSegmentRules?.rules?.filter((itm: any) => itm?.evalResult)[0]?.osnPath || "";

        if (osnPath !== "") {
            urlParams.set("osn", osnPath);
        }

        if (additionalQS !== "") {
            setAdditionalQS(urlParams, additionalQS);
        }

        setCDWParamsToButton(urlParams, offer, buttonSection);
        const cParam = cartUrl.includes("?") ? "&" : "?";
        return cartUrl + cParam + urlParams.toString();
    };
}

export function registerGetPriceFunction(): void {
    window.getPrice = function (val1, val2, defaultVal) {
        /*   if (window.vueInstance) {
            if (window.vueInstance.vueLoaded === false) {
                return "";
            }
        } */

        if (defaultVal && defaultVal !== "" && val1 === undefined) {
            storeDynamicModals(defaultVal);
            return (
                window.digitalData?.offers?.[eval("digitalData.offersGroup." + defaultVal.split("=")[0])][val2] || ""
            );
        }

        if (val1 === undefined) {
            //Model does not have default value
            return "";
        }

        return window.digitalData?.offers?.[val1]?.[val2] || "";
    };
}

/* Function used in default view to select the default option on page load.
 This function selects the default value for
1. Dropdown
2. Radio button
3. Button Group
 */
function selectDefaultOptionForRadioAndCheckbox(): void {
    //Get all :href's whose value has "=". It selects :href="getCartUrl('dropdownSelected=o1','','none')"
    const nList = document.querySelectorAll("[\\:href*='=']");
    nList.forEach((ele) => {
        //Get the model name. This returns "dropdownSelected=o1"
        const model = (ele.getAttribute(":href") as string)?.match(/'([^']+)'/)?.[1] || "";
        if (model && model !== "" && model.includes("=")) {
            const modelName = model.split("=")[0];
            const selectedVal = model.split("=")[1];
            //Get the nodes which has the attribute: name="dropdownSelected"
            const modelNodeList = document.querySelectorAll("[name*=" + modelName + "]");
            modelNodeList.forEach((ele: any) => {
                //If the node is select dropdown, and if the model value matches, assign "selected" attribute to the element.
                if (ele.type === "select-one") {
                    for (let i = 0; i < ele.options.length; i++) {
                        if (ele.options[i].getAttribute("value") === selectedVal) {
                            ele.options[i].setAttribute("selected", "true");
                            break;
                        }
                    }
                } else if (ele.type === "radio") {
                    //If the node is radio, and if the model value matches, assign "checked" attribute to the element.
                    if (ele.getAttribute("value") === selectedVal) {
                        ele.setAttribute("checked", "true");
                    }
                }
            });
        }
    });
}

function updateGetPriceModels(): void {
    const getPriceNodeList = document.querySelectorAll("[v-html*='offersGroup']");
    if (getPriceNodeList && getPriceNodeList.length > 0) {
        getPriceNodeList.forEach((ele: any) => {
            const vhtml = ele.getAttribute("v-html");
            ele.textContent = eval(vhtml);
        });
    }
}

function selectDropdownChangeHandler(e: any): void {
    if (e.target.type === "radio" || e.target.type === "select-one") {
        const nodeList = document.querySelectorAll("[\\:href*=" + e.target.getAttribute("name") + "]");
        if (nodeList && nodeList.length > 0) {
            const hrefVal = nodeList[0].getAttribute(":href");
            //Model will be "radiobuttonmodel=o1"
            const model = hrefVal?.match(/'([^']+)'/)?.[1] as string;
            //Get the old offer
            const oldOffer = window.digitalData.offersGroup[e.target.name];
            const newOffer = model?.replace(oldOffer, e.target.value);
            //Update digitalData.offersGroup
            window.digitalData.offersGroup[e.target.name] = e.target.value;
            const updatedFunction = hrefVal?.replace(model, newOffer);
            nodeList[0].setAttribute("href", eval(updatedFunction as string));
            updateGetPriceModels();
        }
    }
}

//This function is used only in defaultLifecycle
export function updateBuynowCTA(): void {
    //Below code is to identify all :href components and set href value
    const hrefNodeList = document.querySelectorAll("[\\:href]");
    for (let i = 0; i < hrefNodeList.length; i++) {
        const node = hrefNodeList[i];
        const func: string = node.getAttribute(":href") as string;
        if (func !== "") {
            try {
                node.setAttribute("href", eval(func));
            } catch (err) {
                console.log("Error in assigning href val to node " + node + " with error: " + err);
            }
        }
    }

    selectDefaultOptionForRadioAndCheckbox();
    document.addEventListener("input", selectDropdownChangeHandler);
}

