/* eslint-disable @typescript-eslint/no-explicit-any */
function initializePaymentMethods(): Promise<boolean> {
    return new Promise((resolve) => {
        if (window.digitalData?.payments?.culturePayments) {
            let payments: any[] = [];
            Object.values(window.digitalData.payments?.culturePayments)
                .filter(function (data: any) {
                    return data.isActivePayment === true;
                })
                //Get all payments method from digitalData
                .map(function (dta: any) {
                    if (dta.icon === "" && Object.keys(dta.methods).length > 0) {
                        Object.values(dta.methods).map(function (ta) {
                            payments.push(ta);
                        });
                    } else {
                        payments.push(dta);
                    }
                });
            payments = payments
                .filter(function (dta) {
                    return dta.isActiveMethod === true || dta.isActivePayment === true;
                })
                //Get all active payments methods
                .map(function (data) {
                    return data.title;
                })
                //Get unique paymnets methods using this filter
                .filter(function (value, index, self) {
                    return self.indexOf(value) === index;
                });
            document.querySelectorAll("[data-payment-methods]").forEach(function (elem) {
                if (elem.childElementCount === 0) {
                    let paymentMethods = "";
                    payments.map(function (method) {
                        paymentMethods += `<span id="paymenttypeicon_CreditCard" title="${method}" alt="${method}" data-pimage="${method
                            .toLowerCase()
                            .replace(/ /g, "")}" class="pay-icon ${method
                            .toLowerCase()
                            .replace(/ /g, "")} me-1"></span>`;
                    });
                    elem.innerHTML = paymentMethods;
                }
            });
            resolve(true);
        } else {
            resolve(false);
        }
    });
}
export { initializePaymentMethods };
