/* eslint-disable no-eval */
export function applyPageTitle(): void {
    const titleRulesLength = window.digitalData?.page?.rules?.length || 0;
    for (let i = 0; i < titleRulesLength; i++) {
        try {
            const ret = eval(window.digitalData.page.rules[i].expression);
            if (ret) {
                eval(window.digitalData.page.rules[i].assignment);
                document.title = window.digitalData?.page?.title || document.title;
                break;
            }
        } catch (err) {
            console.log(err);
        }
    }
}

export function registerGetUrl(): void {
    window.getUrl = function (url: string, enableQS = false) {
        if (!url) {
            return "";
        }
        if (url === "") {
            return "";
        }
        if (enableQS) {
            return url + location.search;
        }

        return url;
    };
}

export function updateHref(): void {
    const currentURL = window.location;
    if (currentURL.search === "") {
        return;
    }

    document.querySelectorAll("a").forEach((elem) => {
        if (
            (elem.hostname === currentURL.hostname || elem.hostname.includes("mcafee.com")) &&
            !elem.hostname.includes("service.mcafee.com")
        ) {
            if (elem.pathname === currentURL.pathname) {
                return;
            }
            const tempQS: string[] = [];
            decodeURIComponent(currentURL.search)
                .replace("?", "")
                .replace(/\?/g, "&")
                .replace(/&amp;/g, "&")
                .replace(/&&/g, "&")
                .split("&")
                .map((qs) => {
                    if (!elem.search.includes(qs.split("=")[0])) {
                        tempQS.push(qs);
                    }
                });
            if (elem.search === "") {
                elem.search = `?${tempQS.join("&")}`;
            } else if (tempQS.length > 0) {
                elem.search += `&${tempQS.join("&")}`;
            }
        }
    });
}
