/* eslint-disable no-undef */
class TextScroller {
    toRotate: string[];
    className: string;
    loopNum: number;
    period: number;
    txt: string;
    isDeleting: boolean;
    constructor(className: string, toRotate: string[], period: number) {
        this.toRotate = toRotate;
        this.className = className;
        this.loopNum = 0;
        this.period = period;
        this.txt = "";
        this.startScrolling();
        document.querySelector("." + this.className)?.setAttribute("data-enabled", "true");
        this.isDeleting = false;
    }
    startScrolling() {
        const i = this.loopNum % this.toRotate.length;
        const fullTxt = this.toRotate[i];
        if (this.isDeleting) {
            this.txt = fullTxt.substring(0, this.txt.length - 1);
        } else {
            this.txt = fullTxt.substring(0, this.txt.length + 1);
        }
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        const elem: any = document.querySelector("." + this.className);
        elem.innerText = this.txt;
        // eslint-disable-next-line @typescript-eslint/no-this-alias
        const that = this;
        let delta = 200 - Math.random() * 100;
        if (this.isDeleting) {
            delta /= 2;
        }
        if (!this.isDeleting && this.txt === fullTxt) {
            delta = this.period;
            this.isDeleting = true;
        } else if (this.isDeleting && this.txt === "") {
            this.isDeleting = false;
            this.loopNum++;
            delta = 500;
        }
        setTimeout(function () {
            that.startScrolling();
        }, delta);
    }
}
function initializeTextScroller(): void {
    document.querySelectorAll(".cmp-alert__container__text").forEach(function (elem, index) {
        const toRotate = elem.getAttribute("data-type");
        const period = elem.getAttribute("data-period");
        if (elem.innerHTML.includes("${scroller}")) {
            const scrollerCSS = "cmp-alert__scroller" + index;
            elem.innerHTML = elem.innerHTML.replace(
                "${scroller}",
                "<span class='" + scrollerCSS + "' data-type='" + toRotate + "' data-period=" + period + "></span>",
            );
        }
    });

    document.querySelectorAll("[class*=cmp-alert__scroller]").forEach(function (elem) {
        const toRotate = elem.getAttribute("data-type");
        const className = elem.getAttribute("class") || "";
        const period = elem.getAttribute("data-period") || "";
        const isAlreadyEnabled = elem.getAttribute("data-enabled") || false;
        //Convert seconds to milliseconds
        const timeout = parseInt(period, 10) * 1000 || 2000;
        if (toRotate && toRotate !== "undefined" && toRotate !== "" && !isAlreadyEnabled) {
            new TextScroller(className, toRotate.split(","), timeout);
        }
    });
}
export { initializeTextScroller };
