import { setDigitalData } from "../utilities/digitalData";
import { prepareLinkData } from "../handler";
// eslint-disable-next-line @typescript-eslint/no-explicit-any
let lookupData: any,
    lookupOrder: string[],
    propertyData: [{ tagName: string; event: string; pageView: string; specificEvents: string[] }] | [];
const utagData: AnalyticsViewConfig = {};
function registerViewAndLinkData(): void {
    try {
        if (
            window.digitalData.utagFetch?.config &&
            window.digitalData.utagView?.config &&
            window.digitalData.utagFetch?.order
        ) {
            lookupData = window.digitalData.utagFetch.config;
            lookupOrder = window.digitalData.utagFetch.order;
            propertyData = window.digitalData.utagView.config;
        }
    } catch (err) {
        lookupData = [{}];
        lookupOrder = [];
        propertyData = [];
        console.log(`There was an error while reading Config data.
    Error Stack: ${err.message}`);
    }
    window.digitalData.analytics = window.digitalData.analytics || {};
    /* if (!window.digitalData.analytics) {
        window.digitalData.analytics = {};
    } */
    if (lookupData?.length > 0) {
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        lookupData.map((tagData: any) => {
            const tagProperty = propertyData.find(
                (property: { tagName: string; event: string; pageView: string; specificEvents: string[] }) => {
                    if (property.tagName === tagData.tagName) {
                        return property;
                    }
                },
            );
            if (tagProperty) {
                utagData[tagData.tagName] = {
                    value: tagData["defaultValue"],
                    pageView: tagProperty.pageView,
                    event: tagProperty.event,
                    winner: "defaultValue",
                    specificEvents: tagProperty.specificEvents ? tagProperty.specificEvents : [],
                };
                lookupOrder.forEach((order: string) => {
                    // If the value from window.digitalData island directly apply eval method
                    if (tagProperty && tagData[order]) {
                        try {
                            utagData[tagData.tagName] = {
                                // eslint-disable-next-line no-eval
                                value: eval(tagData[order]),
                                pageView: tagProperty.pageView,
                                event: tagProperty.event,
                                winner: order,
                                specificEvents: tagProperty.specificEvents ? tagProperty.specificEvents : [],
                            };
                        } catch (error) {
                            console.log(`There was an error while processing a tealium tag ${tagData.tagName},
      Error Message: ${error.message}`);
                        }
                    }
                });
            }
        });
    }
    setDigitalData({ key: "digitalData.analytics.utagData", value: utagData });
    try {
        if (!window.digitalData.analytics?.events) {
            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            window.digitalData.utagEvent?.config.map((data: any) => {
                //getting list of event tag from config
                for (const key in data) {
                    if (key === "defaultValue") {
                        //eval the value and set
                        setDigitalData({
                            key: `digitalData.analytics.events.${data.tagName}.value`,
                            // eslint-disable-next-line no-eval
                            value: eval(data[key]),
                        });
                    } else if (key !== "tagName") {
                        //set the properties as it is
                        setDigitalData({
                            key: `digitalData.analytics.events.${data.tagName}.${key}`,
                            value: data[key],
                        });
                    }
                }
            });
        }
    } catch (error) {
        console.log(`There was an error while reading data from event config.
                Error Stack ${error.message}`);
    }
}
function fireViewAndLinkData(): void {
    registerViewAndLinkData();
    const viewData: AnalyticsFireData = {};
    for (const key in utagData) {
        if (
            utagData[key].value &&
            utagData[key].value !== "" &&
            utagData[key].pageView &&
            (utagData[key].pageView === "true" || utagData[key].pageView === true)
        ) {
            viewData[key] = utagData[key].value.toString().toLowerCase();
        }
    }
    window.utag?.view(viewData);
    const linkdata = window.digitalData?.analytics?.events || undefined;
    if (linkdata) {
        for (const key in linkdata) {
            if (linkdata[key].pageLoad && (linkdata[key].pageLoad === "true" || linkdata[key].pageLoad === true)) {
                prepareLinkData(key, linkdata[key], "", "", "");
            }
        }
    }
}
export { registerViewAndLinkData, fireViewAndLinkData };
