/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import { handleAnalytics } from "./handler";
// eslint-disable-next-line @typescript-eslint/no-explicit-any
let analyticsHandler: any;
// eslint-disable-next-line @typescript-eslint/no-explicit-any
function registerEvents(el: any, value: AnalyticsEventInput): void {
    analyticsHandler = (eve: CustomEvent) => {
        handleAnalytics({ event: eve, value: value, element: el });
    };
    Object.keys(value).forEach((eventName) => {
        if (eventName === "append") {
            el.addEventListener(eventName, analyticsHandler);
            const append = new Event("append");
            el.dispatchEvent(append);
        } else {
            //   $(el).on(eventName, handler);
            el.addEventListener(eventName, analyticsHandler);
        }
    });
}
export { registerEvents, analyticsHandler };
