//Vue Instance
export const beforeVueInstanceCreationEvent = /*#__PURE__*/ new CustomEvent("beforeVueInstanceCreationEvent");
export const afterVueInstanceCreationEvent = /*#__PURE__*/ new CustomEvent("afterVueInstanceCreationEvent");

export function vueMountCompletedEvent(vueIns: unknown): CustomEvent {
    return new /*#__PURE__*/ CustomEvent("vueMountCompletedEvent", { detail: { vueInstance: vueIns } });
}
export function registerDirectivesEvent(vueIns: unknown): CustomEvent {
    return new /*#__PURE__*/ CustomEvent("registerDirectivesEvent", { detail: { vueInstance: vueIns } });
}
//API
export const updateDigitalDataEvent = /*#__PURE__*/ new CustomEvent("updateDigitalDataEvent");
export const apiCompletedAndDigitalDataMergedEvent = /*#__PURE__*/ new CustomEvent(
    "apiCompletedAndDigitalDataMergedEvent",
);
//Analytics
export const fireAnalyticsEvent = /*#__PURE__*/ new CustomEvent("fireAnalyticsEvent");
