/* eslint-disable no-underscore-dangle */

import { asyncPOST } from "../api/api";
import { CLIENT_LOGS_CONFIG } from "../constants/commonConstants";

/*
 * Method used to check if logging is enabled for the particular type.
 * logs is a cloud config and the object is available in digitalData.
 * If it is not available, then take the value from default config which is set in constants.
 */
export function logToServer(logType: string): boolean {
    if (window.digitalData.logs) {
        return window.digitalData.logs[logType];
    } else {
        return CLIENT_LOGS_CONFIG[logType];
    }
}

/*
 * Method used to Send the error message to CSP.
 * In author mode, the errors will be shown to user via error ribbon. Else, the error
 * is sent to CSP for logging.
 */
function sendErrorLogsToServer(
    errorMsg: string,
    msg: string,
    sendLogsToServer: boolean /* , vueLoaded: boolean */,
): void {
    const errorDetails = {
        ErrorMsg: errorMsg,
    };
    const isAuthorMode = window.digitalData?.page?.isAuthorMode || false;
    if (isAuthorMode) {
        if (!window.clientErrorDetails.includes(msg)) {
            window.clientErrorDetails.push(msg);
        }
    } else {
        if (sendLogsToServer /*  && vueLoaded */) {
            const errServletURL = window.location.pathname.replaceAll(".html", ".error.nocache.html");
            let optionalHeader = {};
            if (window.__uuid) {
                optionalHeader = { "X-UUID": window.__uuid };
            }
            asyncPOST(errServletURL, errorDetails, optionalHeader).catch(function (error) {
                window.console.log(error);
            });
        }
    }
}
/*
 * Method used to log the error messages
 */
export function logErrorMessage(
    type: string,
    msg: string,
    stack: string,
    cmpName: string,
    additionalInfo: string,
    sendLogsToServer: boolean /* ,
    vueLoaded: boolean, */,
): void {
    let message = type + "\n";
    message += msg !== "" ? "Message: " + msg + "\n" : "";
    message += stack !== "" ? "Stack Trace: " + stack + "\n" : "";
    message += cmpName !== "" ? "Component: " + cmpName + "\n" : "";
    message += additionalInfo !== "" ? "Additional Info : " + additionalInfo + "\n" : "";
    console.log(message);
    sendErrorLogsToServer(message, msg, sendLogsToServer /* , vueLoaded */);
}

/*
 * Method used to log the global errors.
 * https://developer.mozilla.org/en-US/docs/Web/API/GlobalEventHandlers/onerror
 */
function logGlobalError(msg: string, url: string, line: string, col: string, error: Error): boolean {
    let additionalInfo = "";
    additionalInfo += url !== "" ? "File Name: " + url + "\n" : "";
    additionalInfo += line !== "" ? "Line Number: " + line + "\n" : "";
    additionalInfo += col !== "" ? "Column Number: " + col + "\n" : "";
    const logGlobalErr = window.digitalData?.logs?.globalError || false;
    logErrorMessage(
        "[Global JS Error Handler]:",
        msg,
        error && error.stack ? error.stack : "",
        "",
        additionalInfo,
        logGlobalErr,
        /* true, */
    );
    // if  true, this prevents firing of the default event handler.
    return false;
}

/*
 * Method used to Register Global Event Handler and to initialize clientError message array
 */
export function registerGlobalErrorHandler(): void {
    window.onerror = logGlobalError;
    window.clientErrorDetails = [];
}
