/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable no-eval */
/* eslint-disable @typescript-eslint/no-explicit-any */
import { createApp, reactive } from "vue";
import { afterVueInstanceCreationEvent, vueMountCompletedEvent, registerDirectivesEvent } from "../events/customEvents";
import { logErrorMessage, logToServer } from "../error-handler/globalErrorHandler";
import { ERROR_TYPE, ERROR_MESSAGES, CLIENT_LOG_PROP } from "../constants/commonConstants";

/* Method used to create a Vue instance and can be accessible in window level (window.vueInstance)
window.digitalData is made reactive  */
export function createVueInstance(
    dta: Record<any, any>,
    app: string,
    vueInstance: string,
    methods?: {},
    computedProp?: {},
    components?: {},
    mountOn?: string,
    store?: {},
    directives?: {},
    setup?: () => {},
): Promise<boolean> {
    return new Promise((resolve) => {
        console.time("Vue Instance Creation");
        window[app] = createApp({
            setup() {
                if (setup) {
                    return setup();
                }
            },
            data() {
                return {
                    dta,
                };
            },
            computed: computedProp,
            methods,
            components,
            mounted() {
                document.dispatchEvent(vueMountCompletedEvent(this));
            },
            directives: directives ? directives : {},
        });
        if (store) {
            window[app].use(store);
        }
        window[app].config.globalProperties.digitalData = reactive(window.digitalData);
        window[app].config.errorHandler = function (err: any) {
            logErrorMessage(
                ERROR_TYPE.dataError,
                ERROR_MESSAGES.unexpectedVueError + ":" + err,
                "",
                "vueInstance.ts, " + err,
                "",
                logToServer(CLIENT_LOG_PROP.dataError),
            );
        };
        window.digitalData = window[app].config.globalProperties.digitalData;
        document.dispatchEvent(registerDirectivesEvent(window[app]));
        window[vueInstance] = window[app].mount(mountOn);
        console.timeEnd("Vue Instance Creation");
        document.dispatchEvent(afterVueInstanceCreationEvent);
        resolve(true);
    });
}
