import { registerEvents } from "./registerEvents";
import { removeRegisterdEvents } from "./removeRegisterdEvents";
import { fireViewAndLinkData, registerViewAndLinkData } from "./helper/helperFunctions";
import { DirectiveBinding, VNode } from "vue";

// eslint-disable-next-line @typescript-eslint/no-explicit-any
function registerDirective(vueInstance: any): void {
    if (!vueInstance) {
        return;
    }
    vueInstance.directive("triggerAnalytics", {
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        beforeMount(el: any, binding: DirectiveBinding, vnode: VNode) {
            let inputData;
            console.log("Directive Mount");
            try {
                inputData = JSON.parse(binding.value);
            } catch (error) {
                inputData = binding.value;
                console.log(`There was an error in given JSON data
                Error Stack: ${error.message}`);
            }
            registerEvents(el, inputData);
            console.log(vnode, inputData);
        },
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        unmounted(el: any, binding: DirectiveBinding, vnode: VNode) {
            let inputData;
            try {
                inputData = JSON.parse(binding.value);
            } catch (error) {
                inputData = binding.value;
                console.log(`There was an error in given JSON data
                Error Stack: ${error.message}`);
            }
            console.log(vnode);
            removeRegisterdEvents(el, inputData);
        },
    });
}

function registerAnalyticsHandler(): void {
    registerViewAndLinkData();
    if (!window.atfVueInstance && !window.btfVueInstance) {
        document.querySelectorAll("[data-trigger-analytics]").forEach((elem) => {
            let inputData;
            console.log("No Vue Analytics Mount");
            try {
                inputData = JSON.parse(elem.getAttribute("data-trigger-analytics") || "");
            } catch (error) {
                inputData = {};
                console.log(`There was an error in given JSON data
                Error Stack: ${error.message}`);
            }
            registerEvents(elem, inputData);
        });
    }
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    document.addEventListener("registerDirectivesEvent", (eve: any) => {
        registerDirective(eve.detail.vueInstance);
    });
    document.addEventListener("fireAnalyticsEvent", () => {
        const timer = setInterval(() => {
            if (window.utag) {
                clearInterval(timer);
                fireViewAndLinkData();
            }
        }, 100);
    });
}
export { registerAnalyticsHandler };
