/* eslint-disable no-eval */
/* eslint-disable @typescript-eslint/no-explicit-any */
import { logErrorMessage, logToServer } from "../../../core/error-handler/globalErrorHandler";
import { ERROR_TYPE, ERROR_MESSAGES, CLIENT_LOG_PROP } from "../../../core/constants/commonConstants";
export const offerSegmentRules: Record<any, any> = {
    offerEvalRules(): Promise<boolean> {
        console.time("Eval offersegment rules");
        return new Promise((resolve) => {
            const offSegRules = this.digitalData?.offerSegmentRules?.rules?.length || undefined;
            if (offSegRules) {
                for (const off of this.digitalData.offerSegmentRules.rules) {
                    try {
                        const exp = eval(off.expression);
                        if (exp) {
                            this.evalRules(off);
                            resolve(true);
                            break;
                        }
                    } catch (err) {
                        this.evalRules(
                            this.digitalData.offerSegmentRules.rules[
                                this.digitalData.offerSegmentRules.rules.length - 1
                            ],
                        );
                        resolve(false);
                    }
                }
            } else {
                logErrorMessage(
                    ERROR_TYPE.dataError,
                    ERROR_MESSAGES.offRulesNotAvailable,
                    "",
                    "offerSegmentRules.ts",
                    "",
                    logToServer(CLIENT_LOG_PROP.dataError) /* ,
                    window.vueInstance.vueLoaded, */,
                );
                resolve(false);
            }
        });
    },
    evalRules(off: any): void {
        off.evalResult = true;
        // the below format is required as eval will fail if the JSONObject has formats like below
        //digitalData.offers.o1.rewards="[{"DiscountTypeValue":"None"}]"
        //digitalData.offers.o1.rewardTotal="{"DiscountTypeValue":"None"}"
        //So after doing the transformation, formatted will look like below.
        //digitalData.offers.o1.rewards=[{"DiscountTypeValue":"None"}]
        //digitalData.offers.o1.rewardTotal={"DiscountTypeValue":"None"}
        const formattedAssignment = off.assignment
            .replaceAll('"[', "[")
            .replaceAll(']"', "]")
            .replaceAll('"{', "{")
            .replaceAll('}"', "}");
        eval(formattedAssignment);
    },
};
