/* eslint-disable @typescript-eslint/no-explicit-any */
import { analyticsHandler } from "./registerEvents";
// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
function removeRegisterdEvents(element: any, value: AnalyticsEventInput): void {
    Object.keys(value).forEach((eventName) => {
        element.removeEventListener(eventName, analyticsHandler);
    });
}
export { removeRegisterdEvents };
