/**
 *
 * @param data key Value Pair
 * key should startwith digitalData
 */
function setDigitalData(data: { key: string; value: unknown }): void {
    const stringToPath = function (path: string): string[] {
        // If the path isn't a string, return it
        if (typeof path !== "string") {
            return path;
        }

        // Create new array
        const output: string[] = [];

        // Split to an array with dot notation
        path.split(".").forEach(function (item) {
            // Split to an array with bracket notation
            item.split(/\[([^}]+)\]/g).forEach(function (key) {
                // Push to the new array
                if (key.length > 0) {
                    output.push(key);
                }
            });
        });

        return output;
    };
    // Remove digitalData. from path
    data.key = data.key.replaceAll("digitalData.", "");
    // Convert the path to an array if not already
    const pathArray: string[] = stringToPath(data.key);

    // Cache the path length and current spot in the object
    const length: number = pathArray.length;
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    let current: any = window.digitalData;

    // Loop through the path
    pathArray.forEach(function (key: string, index: number) {
        // If this is the last item in the loop, assign the value
        if (index === length - 1) {
            current[key] = data.value;
        } else {
            // If the key doesn't exist, create it
            if (!current[key]) {
                current[key] = {};
            }

            // Update the current place in the objet
            current = current[key];
        }
    });
}
// eslint-disable-next-line @typescript-eslint/no-explicit-any
function getDigitalData(path: string): any {
    if (typeof path != "string") {
        return null;
    }
    try {
        // eslint-disable-next-line no-eval
        return eval(path);
    } catch (error) {
        return null;
    }
}
export { setDigitalData, getDigitalData };
