import {
    beforeVueInstanceCreationEvent,
    //  registerDirectivesEvent,
    //  vueMountCompletedEvent,
} from "../../core/events/customEvents";
import { genericPageLifeCycle, /* fetchDynamicData ,*/ storeDynamicOffers } from "./commonLifecycle";
import { createVueInstance } from "../../core/vue/vueInstance";
import { methods } from "../vue/methods/personalizedMethods";
import { computed } from "../vue/computed/personalizedComputed";
import { subscriptionTimer } from "../components/subscriptionTimer";
import { errorDetail } from "../../core/components/error-detail";
import { applyPageTitle, updateHref } from "../features/common-methods";
import { logErrorMessage, logToServer } from "../../core/error-handler/globalErrorHandler";
import { ERROR_TYPE, ERROR_MESSAGES, CLIENT_LOG_PROP } from "../../core/constants/commonConstants";
//import { createApp } from "vue";
import { initializeBootstrapComponents } from "../../core/utils/bootstrapUtils";

export function getComponentsList(): Record<any, any> {
    return {
        "subscription-timer": subscriptionTimer,
        "error-detail": errorDetail,
    };
}

export function personalizedPageLifeCycle(): void {
    genericPageLifeCycle();
    document.dispatchEvent(beforeVueInstanceCreationEvent);
    console.log("Before Vue instance creation");
    createVueInstance(
        {
            digitalData: window.digitalData,
            vueLoaded: false,
        },
        "atfApp",
        "atfVueInstance",
        methods,
        computed,
        getComponentsList(),
        "[data-instance='above-the-fold']",
    )
        .then(function () {
            console.log("Above the fold Vue instance created successfully!");
        })
        .catch(function (err: string) {
            logErrorMessage(
                ERROR_TYPE.dataError,
                ERROR_MESSAGES.vueInstanceCreationFailed,
                "",
                "personalizedLifeCycle.ts, " + err,
                "",
                logToServer(CLIENT_LOG_PROP.dataError),
            );
        });

    // fetchDynamicData();
}

export function apiCompletedAndDigitalDataMergedHandler(): void {
    if (window.atfVueInstance) {
        window.atfVueInstance.offerEvalRules().then(function (response: boolean) {
            console.timeEnd("Eval offersegment rules");
            if (response) {
                console.log("offerSegmentRules eval Success");
            } else {
                console.log("offerSegmentRules eval Failed");
            }
        });
    }

    applyPageTitle();
    updateHref();
}

export function vueMountCompleteHandler(event: any): void {
    event.detail.vueInstance.vueLoaded = true;
}
function renderNoscriptContents() {
    const nos = document.getElementsByTagName("noscript")[0];
    const nosHtml = nos.textContent || nos.innerHTML;
    if (nosHtml) {
        const temp = document.createElement("div") as Element;
        temp.innerHTML = nosHtml;
        const btf = temp?.querySelector(".below-the-fold") as Element;
        btf?.setAttribute("data-instance", "below-the-fold");
        document.querySelector("[data-instance='above-the-fold']")?.after(btf);
        storeDynamicOffers();
        createVueInstance(
            {
                digitalData: window.digitalData,
                vueLoaded: false,
            },
            "btfApp",
            "btfVueInstance",
            methods,
            computed,
            getComponentsList(),
            "[data-instance='below-the-fold']",
        )
            .then(function () {
                console.log("Below the fold Vue instance created successfully!");
                initializeBootstrapComponents();
            })
            .catch(function (err: string) {
                logErrorMessage(
                    ERROR_TYPE.dataError,
                    ERROR_MESSAGES.vueInstanceCreationFailed,
                    "",
                    "personalizedLifeCycle.ts, " + err,
                    "",
                    logToServer(CLIENT_LOG_PROP.dataError),
                );
            });
    }
    window.removeEventListener("scroll", renderNoscriptContents);
}
if (document.querySelector(".noscriptcontainer noscript")) {
    window.addEventListener("scroll", renderNoscriptContents);
}
