/* eslint-disable @typescript-eslint/no-explicit-any */
import { registerAnalyticsHandler } from "../../core/analytics/main";
import { registerGlobalErrorHandler } from "../../core/error-handler/globalErrorHandler";
import { asyncGETAll } from "../../core/api/api";
import { logErrorMessage, logToServer } from "../../core/error-handler/globalErrorHandler";
import { ERROR_TYPE, ERROR_MESSAGES, CLIENT_LOG_PROP, API_URL_CONFIG } from "../../core/constants/commonConstants";
import {
    updateDigitalDataEvent,
    apiCompletedAndDigitalDataMergedEvent,
    fireAnalyticsEvent,
} from "../../core/events/customEvents";
import { initializeBootstrapComponents, bs5ToggleClickHandler } from "../../core/utils/bootstrapUtils";
import { registerGetUrl } from "../features/common-methods";
import { registerGetCartUrl, registerGetPriceFunction } from "../features/addToCartFeature";

String.prototype.replaceDash = function (): string {
    return this.replace(/\s/gi, "-");
};

String.prototype.replaceAll = function (search: any, replacement: any): string {
    return this.split(search).join(replacement);
};

export function domContentLoadedHandler(): void {
    console.log("DOM Content loaded Handler");
    initializeBootstrapComponents();
    document.addEventListener("click", bs5ToggleClickHandler);
}

function registerAnalyticsEvents(): void {
    registerAnalyticsHandler();
}

function initializeDynamicObjects(): void {
    window.digitalData.offersGroup = {};
    window.digitalData.showModelPopup = "";
    window.digitalData.redirectActions = "";
    if (location.search.includes("affid")) {
        window.digitalData.affiliate = { channel: "" };
    } else {
        window.digitalData.affiliate = { affid: "0", channel: "" };
    }
}

export function storeDynamicOffers(): void {
    const customOffers = document.querySelectorAll("[data-offersgroup]");
    if (customOffers) {
        customOffers.forEach((el) => {
            const off = el.getAttribute("data-offersgroup") as string;
            let parsedObj = {};
            try {
                parsedObj = JSON.parse(off);
            } catch (e: any) {
                logErrorMessage(
                    ERROR_TYPE.dataError,
                    ERROR_MESSAGES.jsonParseError,
                    "",
                    "commonLifecycle.ts, Unable to parse JSON : " + off,
                    "",
                    logToServer(CLIENT_LOG_PROP.dataError),
                );
            }

            window.digitalData.offersGroup = {
                ...window.digitalData.offersGroup,
                ...parsedObj,
            };
        });
    }
}

function getDigitalDataURLs(): string[] {
    const urls: Array<string> = [];
    //Frame cacheableURL
    let framedUrl = "",
        qs = "";
    qs = new URLSearchParams(window.location.search).toString();
    if (qs !== "") {
        qs = "?" + qs;
    }
    const cacheableDataLayers =
        API_URL_CONFIG.cacheable + window.digitalData?.cacheableDataLayers?.map((dta: string) => "/" + dta).join("") ||
        "";
    if (cacheableDataLayers !== "") {
        framedUrl = encodeURI(cacheableDataLayers + qs);
        urls.push(framedUrl);
    }

    //Frame non-cacheableURL
    const nonCacheableDataLayers =
        API_URL_CONFIG.nonCacheable +
            window.digitalData?.nonCacheableDataLayers?.map((dta: string) => "/" + dta).join("") || "";
    if (nonCacheableDataLayers !== "") {
        framedUrl = encodeURI(nonCacheableDataLayers + qs);
        urls.push(framedUrl);
    }

    return urls;
}

export function fetchDynamicData(): void {
    console.time("Dynamic data fetch call:");
    document.dispatchEvent(updateDigitalDataEvent);

    asyncGETAll(getDigitalDataURLs())
        .then((responseObj) => {
            console.timeEnd("Dynamic data fetch call:");
            window.digitalData = Object.assign(window.digitalData, responseObj);
            document.dispatchEvent(apiCompletedAndDigitalDataMergedEvent);
            document.dispatchEvent(fireAnalyticsEvent);
        })
        .catch((err) => {
            logErrorMessage(
                ERROR_TYPE.dataError,
                ERROR_MESSAGES.restCallFailed,
                "",
                "commonLifecycle.ts, " + JSON.stringify(err),
                "",
                logToServer(CLIENT_LOG_PROP.dataError),
            );
        });
}

function populateQSisland(): void {
    const pairs: string[] = location.search.slice(1).split("&");
    // eslint-disable-next-line prefer-const
    let result: Record<any, any> = {};
    pairs.forEach(function (pair: string) {
        const pr = pair.split("=");
        result[pr[0]] = decodeURIComponent(pr[1] || "");
    });

    window.digitalData.qs = JSON.parse(JSON.stringify(result));
}

function removeGetCartUrlHref(): void {
    //Removing href from buynow button as
    //1. Vue is complaining about duplicate attribute and things are not working in Edge 42 but working in Chrome and chromium based EDGE browser
    //2. For Default lifecycle, It will be updated on page load.
    //3. Rhino's replacement will work if JS is disabled.
    document.querySelectorAll("[\\:href*=" + "getCartURL" + "]")?.forEach(function (e) {
        e?.removeAttribute("href");
    });
}

export function genericPageLifeCycle(): void {
    registerGlobalErrorHandler();
    registerAnalyticsEvents();
    initializeDynamicObjects();
    storeDynamicOffers();
    populateQSisland();
    registerGetCartUrl();
    registerGetUrl();
    registerGetPriceFunction();
    removeGetCartUrlHref();
}
