/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
/* eslint-disable @typescript-eslint/no-explicit-any */
import { defineComponent, onMounted, ref, computed, Ref } from "vue";

export const errorDetail = defineComponent({
    template: `
    <div v-cloak :class="{'d-none':hideComponent == true}" class="cmp-errordetails" v-if="getPageErrorConditions()">
    <div class="alert alert-danger">     
      <strong>Problems on the page</strong>            
      <span class="cmp-errordetails__close-btn" @click="hideComponent=true">X</span>
      <hr class="message-inner-separator" />
      <div id="accordion" class="cmp-errordetails_container">
        <div
          v-for="(itm, key, index) in islandWithError"
          :key="index"
          class="cmp-errordetails__body"
        >        
          <a
            class="collapsed cmp-errordetails__link"
            data-bs-toggle="collapse"
            :href="'#' + key + 'Error'"
            >Related to {{ key }}:</a
          >
          <div
            :id="key + 'Error'"
            class="collapse cmp-errordetails__msg"
            data-bs-parent="#accordion"
          >
            <ul>
              <li
                v-for="(item, indx) in itm"
                :key="indx"
              >
                {{ item.errorMessage }}
              </li>
            </ul>
          </div>
        </div>
        <div
          class="cmp-errordetails__body"
          v-if="
            clientErrorDetails && clientErrorDetails.length > 0 ? true : false
          "
        >
          <a
            class="collapsed cmp-errordetails__link"
            data-bs-toggle="collapse"
            href="#clientErrors"
            >Related to client errors:</a
          >
          <div
            id="clientErrors"
            class="collapse cmp-errordetails__msg"
            data-bs-parent="#accordion"
          >
            <ul>
              <li v-for="(item, idc) in clientErrorDetails" :key="idc">
                {{ item }}
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
  `,
    props: {
        dataDigitalDataIslanders: String,
    },
    setup(props) {
        const clientErrorDetails: Ref<string[]> = ref([]);
        const hideComponent = ref(false);
        const islandWithError = ref({});

        //computed
        const ddIslanders = computed(() => {
            if (props.dataDigitalDataIslanders && props.dataDigitalDataIslanders.length > 0) {
                return props.dataDigitalDataIslanders.split(",");
            }
            return [];
        });

        //Methods
        const setFilteredIslanders = () => {
            if (window.digitalData.page && window.digitalData.page.isAuthorMode) {
                for (let i = 0; i < ddIslanders.value.length; i++) {
                    const island: string = ddIslanders.value[i];

                    if (
                        window.digitalData[island] &&
                        window.digitalData[island]["errorDetails"] &&
                        window.digitalData[island]["errorDetails"].length > 0
                    ) {
                        islandWithError.value[island] = window.digitalData[island]["errorDetails"];
                    }
                }
            }
        };

        const getPageErrorConditions = () => {
            let pageError = false;
            if (window.digitalData.page && window.digitalData.page.isAuthorMode) {
                for (let i = 0; i < ddIslanders.value.length; i++) {
                    const island = ddIslanders.value[i];

                    if (
                        window.digitalData[island] &&
                        window.digitalData[island]["errorDetails"] &&
                        window.digitalData[island]["errorDetails"].length > 0
                    ) {
                        pageError = true;
                        break;
                    }
                }
            }

            if (window.clientErrorDetails && window.clientErrorDetails.length > 0) {
                Object.assign(clientErrorDetails.value, window.clientErrorDetails);
                return true;
            }
            return pageError;
        };

        onMounted(() => {
            setFilteredIslanders();
            clientErrorDetails.value = [...window.clientErrorDetails];
        });

        return {
            clientErrorDetails,
            getPageErrorConditions,
            hideComponent,
            islandWithError,
        };
    },
});
