import "bootstrap/js/dist/collapse";
import "bootstrap/js/dist/carousel";
import "bootstrap/js/dist/tab";
import { initializeTextScroller } from "./textScroller";
import { initializePaymentMethods } from "./paymentMethods";
function accordionAndCollapseHandler() {
    document.querySelectorAll("[data-accordion='accordion']").forEach(function (elem, index) {
        const id = "cmp-accordionsection__item" + index;
        elem.setAttribute("id", id);
        elem.parentElement?.querySelector("a")?.setAttribute("href", "#" + id);
    });

    document.querySelectorAll(".cmp-collapse__title__expandLabel").forEach(function (element: any) {
        element.style.display = "none";
    });

    document.querySelectorAll(".cmp-collapse__container").forEach(function (elem) {
        elem.previousElementSibling?.classList.add("cmp-accordionsection__container--hideborder");
    });

    document.querySelectorAll("[data-collapse='collapse']").forEach(function (elem, index) {
        const id = "cmp-collapsesection__item" + index;
        elem.querySelector(".cmp-collapse__content__holder")?.setAttribute("id", id);
        elem.querySelector(".cmp-collapse__content__holder")?.classList.remove("show");
        elem.querySelector(".cmp-collapse__title__collapseLabel")?.setAttribute("data-bs-target", "#" + id);
        elem.querySelector(".cmp-collapse__title__expandLabel")?.setAttribute("data-bs-target", "#" + id);
    });

    document.querySelectorAll("div[id^='cmp-collapsesection__item']").forEach(function (element) {
        element.addEventListener("hidden.bs.collapse", function () {
            if (!element.classList.contains("show")) {
                const collapseLabel: any = element.parentElement?.parentElement?.querySelector(
                    ".cmp-collapse__title__collapseLabel",
                );
                const expandLabel: any = element.parentElement?.parentElement?.querySelector(
                    ".cmp-collapse__title__expandLabel",
                );
                expandLabel.style.display = "none";
                collapseLabel.style.display = "block";
                element.parentElement?.previousElementSibling?.classList.add(
                    "cmp-accordionsection__container--hideborder",
                );
            }
        });
    });
    document.querySelectorAll("div[id^='cmp-collapsesection__item']").forEach(function (element) {
        element.addEventListener("show.bs.collapse", function () {
            element.parentElement?.previousElementSibling?.classList.remove(
                "cmp-accordionsection__container--hideborder",
            );
        });
    });
    document.querySelectorAll("a[data-bs-toggle='collapse']").forEach(function (element) {
        element.addEventListener("click", function (eve) {
            eve.preventDefault();
        });
    });

    document.querySelectorAll("div[id^='cmp-collapsesection__item']").forEach(function (element) {
        element.addEventListener("shown.bs.collapse", function () {
            const collapseLabel: any = element.parentElement?.parentElement?.querySelector(
                ".cmp-collapse__title__collapseLabel",
            );
            const expandLabel: any = element.parentElement?.parentElement?.querySelector(
                ".cmp-collapse__title__expandLabel",
            );
            expandLabel.style.display = "block";
            collapseLabel.style.display = "none";
        });
    });
}
function carouselHandler() {
    document.querySelectorAll(".carousel").forEach(function (element) {
        element.addEventListener("slide.bs.carousel", function (eve: any) {
            const totalSlides = element.querySelectorAll(".carousel-item").length - 1;
            if (eve.to === 0) {
                document
                    .querySelector('[href="#' + element.id + '"][data-bs-slide="prev"]')
                    ?.classList.remove("invisible");
                document
                    .querySelector('[href="#' + element.id + '"][data-bs-slide="prev"]')
                    ?.classList.add("invisible");
                document
                    .querySelector('[href="#' + element.id + '"][data-bs-slide="next"]')
                    ?.classList.remove("invisible");
            } else if (eve.to === totalSlides) {
                document
                    .querySelector('[href="#' + element.id + '"][data-bs-slide="next"]')
                    ?.classList.remove("invisible");
                document
                    .querySelector('[href="#' + element.id + '"][data-bs-slide="next"]')
                    ?.classList.add("invisible");
                document
                    .querySelector('[href="#' + element.id + '"][data-bs-slide="prev"]')
                    ?.classList.remove("invisible");
            } else {
                document
                    .querySelector('[href="#' + element.id + '"][data-bs-slide="next"]')
                    ?.classList.remove("invisible");
                document
                    .querySelector('[href="#' + element.id + '"][data-bs-slide="prev"]')
                    ?.classList.remove("invisible");
            }
        });
    });
}
function tabHandler() {
    document.querySelectorAll("a[data-bs-toggle='tab']").forEach(function (element) {
        element.addEventListener("show.bs.tab", function (eve: any) {
            eve.relatedTarget.parentElement.classList.remove("active-item");
            eve.target.parentElement.classList.add("active-item");
            const parentTab = eve.target.getAttribute("data-parent-tab");
            if (parentTab) {
                const tabAnchor: any = document.querySelector("#" + parentTab + "[data-bs-toggle='tab']");
                if (tabAnchor) {
                    tabAnchor.click();
                }
            }
        });
    });
    const tabAnchor: any = document.querySelector(window.location.hash + "[data-bs-toggle='tab']");
    if (tabAnchor) {
        tabAnchor.click();
    }
}
/**
 * querySelectorAll(":scope") is not supproted in
 * Non Chromium based Edge browser v45.
 * This was used in Bootstrap 5 Tab component
 * So adding polyfill to Support Tab functions
 * on Non Chromium based Edge browsers
 */
function scopePolyfillForNonChromiumEdge() {
    // querySelectorAll(":scope") polyfill starts here
    try {
        // test for scope support
        document.querySelector(":scope *");
    } catch (error) {
        (function (elementPrototype) {
            // scope regex
            const scope = /:scope(?![\w-])/gi;
            function polyfill(this: any, qsa: any) {
                return function (this: any, selectors: any) {
                    // whether the selectors contain :scope
                    const hasScope = selectors && scope.test(selectors);
                    if (hasScope) {
                        // fallback attribute
                        const attr = "q" + Math.floor(Math.random() * 9000000) + 1000000;

                        // replace :scope with the fallback attribute
                        // eslint-disable-next-line prefer-rest-params
                        arguments[0] = selectors.replace(scope, "[" + attr + "]");

                        // add the fallback attribute
                        this.setAttribute(attr, "");

                        // results of the qsa
                        // eslint-disable-next-line prefer-rest-params
                        const elementOrNodeList = qsa.apply(this, arguments);

                        // remove the fallback attribute
                        this.removeAttribute(attr);

                        // return the results of the qsa
                        return elementOrNodeList;
                    } else {
                        // return the results of the qsa
                        // eslint-disable-next-line prefer-rest-params
                        return qsa.apply(this, arguments);
                    }
                };
            }

            // polyfill Element#querySelector
            const querySelectorWithScope = polyfill(elementPrototype.querySelector);

            // eslint-disable-next-line @typescript-eslint/no-unused-vars
            elementPrototype.querySelector = function querySelector(selectors: any) {
                // eslint-disable-next-line prefer-rest-params
                const args: any = arguments;
                return querySelectorWithScope.apply(this, args);
            };

            // polyfill Element#querySelectorAll
            const querySelectorAllWithScope: any = polyfill(elementPrototype.querySelectorAll);

            // eslint-disable-next-line @typescript-eslint/no-unused-vars
            elementPrototype.querySelectorAll = function querySelectorAll(selectors: any) {
                // eslint-disable-next-line prefer-rest-params
                return querySelectorAllWithScope.apply(this, arguments);
            };

            // polyfill Element#matches
            if (elementPrototype.matches) {
                const matchesWithScope = polyfill(elementPrototype.matches);

                // eslint-disable-next-line @typescript-eslint/no-unused-vars
                elementPrototype.matches = function matches(selectors) {
                    // eslint-disable-next-line prefer-rest-params
                    const args: any = arguments;
                    return matchesWithScope.apply(this, args);
                };
            }

            // polyfill Element#closest
            if (elementPrototype.closest) {
                const closestWithScope = polyfill(elementPrototype.closest);

                // eslint-disable-next-line @typescript-eslint/no-unused-vars
                elementPrototype.closest = function closest(selectors: any) {
                    // eslint-disable-next-line prefer-rest-params
                    const args: any = arguments;
                    return closestWithScope.apply(this, args);
                };
            }
        })(Element.prototype);
    }
}
export function initializeBootstrapComponents(): Promise<boolean> {
    return new Promise((resolve) => {
        scopePolyfillForNonChromiumEdge();
        initializeTextScroller();
        initializePaymentMethods();
        accordionAndCollapseHandler();
        carouselHandler();
        tabHandler();
        resolve(true);
    });
}
export function bs5ToggleClickHandler(e: MouseEvent): void {
    let target = e.target as HTMLElement;
    const closestEle = target.closest("[data-bs-toggle*='dropdown']");
    if (closestEle) {
        target = closestEle as HTMLElement;
    }
    if (target?.hasAttribute("data-bs-toggle") && target.getAttribute("data-bs-toggle") === "dropdown") {
        if (target?.classList?.contains("show")) {
            target?.classList?.remove("show");
            target.nextElementSibling?.classList?.remove("show");
        } else {
            target?.classList?.add("show");
            target.nextElementSibling?.classList?.add("show");

            //Other than the target, hide other dropdowns.
            const dropDown = document.querySelectorAll("[data-bs-toggle*='dropdown']");
            if (dropDown) {
                dropDown.forEach((el: Element) => {
                    if (el !== target) {
                        el?.classList?.remove("show");
                        el.nextElementSibling?.classList?.remove("show");
                    }
                });
            }
        }
    } else {
        //Mouse click other than dropdown toggle
        const dropDown = document.querySelectorAll("[data-bs-toggle*='dropdown']");
        if (dropDown) {
            dropDown.forEach((el: Element) => {
                el?.classList?.remove("show");
                el.nextElementSibling?.classList?.remove("show");
            });
        }
    }
}
